import {createSlice, PayloadAction} from '@reduxjs/toolkit';
import {TypeWork} from "../models/TypeWork";


export interface ListTypeWorksState {
    listTypeWork:TypeWork[]
}


const initialState: ListTypeWorksState = {
    listTypeWork: []

}

export const listTypeWorksSlice = createSlice({
    name: 'listTypeWork',
    initialState,
    reducers: {
        listTypeWorkApp(state, action: PayloadAction<TypeWork[]>) {
            state.listTypeWork = action.payload
        },


    }
})

export default listTypeWorksSlice.reducer;