import {createSlice, PayloadAction} from '@reduxjs/toolkit';
import {VarietyWorkType} from "../models/VarietyWorkType";


export interface varietyWorkState {
    varietyWork: VarietyWorkType
}


const initialState: varietyWorkState = {
    varietyWork: {
        id: null,
        name_variety: "",
        cost: 100,
        duration_in_minutes: 15,
        type_work: ""

    }

}

export const varietyWorkSlice = createSlice({
    name: 'varietyWork',
    initialState,
    reducers: {
        varietyWorkApp(state, action: PayloadAction<VarietyWorkType>) {
            state.varietyWork = action.payload
        },
        varietyWorkName(state, action: PayloadAction<string>) {
            state.varietyWork.name_variety = action.payload
        },
        varietyWorkCost(state, action: PayloadAction<number>) {
            state.varietyWork.cost = action.payload
        },
        varietyWorkMinutes(state, action: PayloadAction<number>) {
            state.varietyWork.duration_in_minutes = action.payload
        },
        varietyWorkType(state, action: PayloadAction<string>) {
            state.varietyWork.type_work = action.payload
        },
        varietyWorkReset(state) {
            state.varietyWork = initialState.varietyWork
        },


    }
})

export default varietyWorkSlice.reducer;