import {createSlice, PayloadAction} from '@reduxjs/toolkit';
import {VarietyWorkType} from "../models/VarietyWorkType";


export interface ListTypeWorksState {
    listVarietyWork:VarietyWorkType[]
}


const initialState: ListTypeWorksState = {
    listVarietyWork: []

}

export const listVarietyWorksSlice = createSlice({
    name: 'listVarietyWork',
    initialState,
    reducers: {
        listVarietyWorkApp(state, action: PayloadAction<VarietyWorkType[]>) {
            state.listVarietyWork = action.payload
        },


    }
})

export default listVarietyWorksSlice.reducer;