import {createSlice, PayloadAction} from '@reduxjs/toolkit';
import {TableRowType} from "../models/TableRowType";
import {TypeWork} from "../models/TypeWork";


export interface TypeWorkState {
    typeWork:TypeWork
}


const initialState: TypeWorkState = {
    typeWork: {
        name_type: "",
        id: null
    }

}

export const typeWorkSlice = createSlice({
    name: 'typeWork',
    initialState,
    reducers: {
        typeWorkApp(state, action: PayloadAction<TypeWork>) {
            state.typeWork = action.payload
        },
        typeWorkName(state, action: PayloadAction<string>) {
            state.typeWork.name_type = action.payload
        },
        typeWorkReset(state) {
            state.typeWork = initialState.typeWork
        },


    }
})

export default typeWorkSlice.reducer;