import {combineReducers, configureStore} from "@reduxjs/toolkit";
import {postApi} from "../../providers/api/RtkService";
import themeAppSlice from "../../providers/api/slice/ThemeSlice";
import authAppSlice from "../../providers/api/slice/AuthSlice";
import tableAppSlice from "../../providers/api/slice/TableSlice";
import typeWorkSlice from "../../providers/api/slice/TypeWorkSlice";
import listTypeWorksSlice from "../../providers/api/slice/ListTypeWorksSlice";
import listVarietyWorksSlice from "../../providers/api/slice/ListVarietyWorkSlice";
import varietyWorksSlice from "../../providers/api/slice/VarietyWorkSlice"

const rootReducer = combineReducers({

    themeAppSlice,
    authAppSlice,
    tableAppSlice,
    typeWorkSlice,
    listTypeWorksSlice,
    listVarietyWorksSlice,
    varietyWorksSlice,
    [postApi.reducerPath]: postApi.reducer
})

export const setupStore =() => {
    return configureStore({
        reducer: rootReducer,
        middleware: (getDefaultMiddleware) =>
        getDefaultMiddleware().concat(postApi.middleware)
    })
}
export type RootState = ReturnType<typeof  rootReducer>
export type AppStore = ReturnType<typeof setupStore>
export type  AppDispatch = AppStore["dispatch"]
